import { Inline } from '@ali/4ever-cangjie';
import {
  JsonML,
  getTagName,
  getAttributes,
} from '@ali/4ever-utils';
import Tag from '../models';

export default {
  name: 'tag',
  match(node: JsonML): boolean {
    return getTagName(node) === 'tag';
  },
  convert(node: JsonML): Inline {
    return Tag.create({
      data: {
        tagType: 'im',
        ...getAttributes(node),
      },
    });
  },
};
